    aeAensMarketPutName: async function (
        secretKey,
        ctAddress,
        name,
        amount,
        overHeight
    ) {
      try {
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: {secretKey: secretKey, publicKey: publicKey},
        });
        await aeSdk.addAccount(senderAccount, {select: true});
        let delegationSignature = await aeSdk.createAensDelegationSignature(
            ctAddress,
            {name}
        );
        let aensMarketContract = await aeSdk.getContractInstance({
          aci: AensMarketContract,
          contractAddress: ctAddress,
        });
        let {decodedResult: maxPrice} =
            await aensMarketContract.methods.get_name_max_price(name);
        let payAmount = 0;
        if (toAettos(amount) > maxPrice) {
          payAmount = Number(amount) / 100;
        }

        const {decodedEvents} = await aensMarketContract.methods.put_name(
            name,
            delegationSignature,
            toAettos(amount),
            Number(overHeight),
            {amount: toAettos(payAmount)}
        );
        console.log(amount);
        console.log(overHeight);
        await this.responseSuccess("aeAensMarketPutName", {
          decodedEvents: decodedEvents,
        });
      } catch (e) {
        this.responseError("aeAensMarketPutName", e.message);
      }
    },

    aeDexSwapExactAeForTokens: async function (
        secretKey,
        ctAddress,
        amountAe,
        amountOutTokenMin,
        path,
        to,
        deadline
    ) {
      try {
        const publicKey = getAddressFromPriv(secretKey);
        const senderAccount = new MemoryAccount({
          keypair: {secretKey: secretKey, publicKey: publicKey},
        });
        await aeSdk.addAccount(senderAccount, {select: true});

        let aensMarketContract = await aeSdk.getContractInstance({
          aci: AedexV2RouterContract,
          contractAddress: ctAddress,
        });
        let swapResult =
            await aensMarketContract.methods.swap_exact_ae_for_tokens(
                toAettos(amountOutTokenMin),
                path,
                to,
                deadline,
                undefined,
                {
                  gas: 150000,
                  omitUnknown: true,
                  amount: toAettos(amountAe),
                }
            );

        this.responseSuccess("aeDexSwapExactAeForTokens", {
          result: swapResult,
        });
      } catch (e) {
        this.responseError("aeDexSwapExactAeForTokens", e.message);
      }
    },
